@-webkit-keyframes appearance-animation {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
        display: flex;
    }

    50% {
        opacity: .5;
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes appearance-animation {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
        display: flex;
    }

    50% {
        opacity: .5;
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@-webkit-keyframes disappearance-animation {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }

    50% {
        opacity: .5;
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

    100% {
        opacity: 0;
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
        display: none;
    }
}

@keyframes disappearance-animation {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }

    50% {
        opacity: .5;
        -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
    }

    100% {
        opacity: 0;
        -webkit-transform: translateX(-40px);
                transform: translateX(-40px);
        display: none;
    }
}
